.back {
  background-color: rgba(213, 243, 239, 0.692);
}

.head {
  height: 4rem;
  width: 66rem;
  background-image: url(./Image/bg-header-desktop.svg);
  background-color: rgba(19, 133, 118, 0.692);
  margin-left: 6rem;
}

.box_setting {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 1050px;
  background-color: white;
  margin: 40px;
  margin-left: 100px;
  box-shadow: 1px 2px 3px black;
} 
.box {
  display: flex;
  font-size: 15px;
  color: rgba(19, 133, 118, 0.692);
  width: 15rem;
  flex-direction: row;
  justify-content: space-between;
  padding: 15px;
}
.first_btn{
  background-color: rgba(19, 133, 118, 0.692);
  border: none;
  color: white;
  width: 28px;
  border-radius: 8px;
  height: 6px;
  font-size: 12px;
  padding: 10px;
} 
.second_btn {
  background-color: black;
  border: none;
  color: white;
  border-radius: 5px;
  height: 15px;
  width:4.3rem;
  font-size: 12px;
  text-align: center;
}

.sen {
  font-size: 20px;
  margin-top: -1px;
}
.se {
  font-size: 20px;
  margin-top: -6px;
  color: rgba(0, 0, 0, 0.451);
}
.span {
  margin-left: 5px;
}

.cover {
  margin-left: -90px;
  border-color: yellow;
}

.lastbutton {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  width: 500px;
}

.button {
  color: rgba(19, 133, 118, 0.692);
  background-color: rgba(165, 229, 220, 0.692);
  border: none;
  padding: 8px;
  border-radius: 10px;
  font-size: 15px;
  margin: 4px;
}
